import React from 'react'
import './App.css';

function Home() {
    return (
        <div>
          <section className="home-section">

              <div className="background">
                  <h3 className='home-heading'>Popular Titles</h3>
              </div>

              <div className="container">
              <div className="reel-section">

                  <div className="reel-one">
                      <img className='reel-image' src="./images/reel.png" alt="reel" />
                      <p>Popular series</p>
                  </div>

                  <div className="reel-two">
                  <img className='reel-image' src="./images/reel.png" alt="reel" />
                  <p>Popular movies</p>
                  </div>
              </div>
              </div>
              
          </section>
        </div>
    )
}

export default Home
