import React from 'react'
import './App.css';
import {Link} from "react-router-dom";

function Nav() {
    return (
        <div>
           <nav>
               <h3>DEMO streaming</h3>
               <ul className='nav-links'>

               <Link to="/">
                   <li className='nav-li'>Home</li>
                   </Link>

                   <Link to="/series">
                   <li className='nav-li'>Series</li>
                   </Link>

                <Link to="/movies">
                   <li className='nav-li'>Movies</li>
                   </Link>
               </ul>
           </nav>
        </div>
    )
}

export default Nav
